<template>
  <v-header>
    <div slot="right" class="h-right" v-if="status==0 || status==2">
      <span v-if="status == 2 && remark" class="img help" @click="showRemark" style="right:.65rem;"></span>
      <a v-link="{name:'order-edit',params:{id:id}}" v-if="status ==2 || status==0" class="mg-r10">编辑</a>
    </div>
  </v-header>

  <v-content :type="!(status==1 && orderType==0) ? 'form':'none'">
    <div class="feedback-box">
      <div v-model="idea" class="feedback">
        <p class="titles">{{categoryName}}协议</p>
        <p class="sn">协议流水号：{{sn}}</p>
      </div>
      <span class="right-tag status0" v-if="status==0"></span>
      <span class="right-tag status4" v-if="status==1"></span>
      <span class="right-tag status1" v-if="status==1 && orderType==1"></span>
      <span class="right-tag status2" v-if="status==2"></span>
      <span class="right-tag status3" v-if="status==10"></span>
      <div class="line"></div>
      <div id="section-box">
        <div class="sections">
          <div class="matter">
            <p class="title">点匠人:</p>
            <div class="edit">
              <p class="text"><a @click="showUserInfo(buyer)" style="color: #00afff;">{{buyName}}</a></p>
            </div>
          </div>
        </div>
        <div class="sections">
          <div class="matter">
            <p class="title">身份证号:</p>
            <div class="edit">
              <p class="text">{{buyIdentityNumber}}</p>
            </div>
          </div>
        </div>
        <div class="sections">
          <div class="matter">
            <p class="title">匠人:</p>
            <div class="edit">
              <p class="text"><a @click="showUserInfo(seller)" style="color: #00afff;">{{sellName}}</a>
              </p>
            </div>
          </div>
        </div>
        <div class="sections">
          <div class="matter">
            <p class="title">身份证号:</p>
            <div class="edit">
              <p class="text">{{sellIdentityNumber}}</p>
            </div>
          </div>
        </div>
        <div class="sections">
          <div class="matter">
            <p class="title">服务名称:</p>
            <div class="edit">
              <p class="text"><a @click="showService(jobId)" style="color: #00afff;">{{serviceName}}</a>
              </p>
            </div>
          </div>
        </div>
        <div class="sections">
          <div class="matter">
            <p class="title">服务期限:</p>
            <div class="edit">
              <p class="text">{{startTime}}-{{endTime}}({{workSplit | week-format}})</p>
            </div>
          </div>
        </div>
        <div class="sections">
          <div class="matter">
            <p class="title">服务时间:</p>
            <div class="edit">
              <p class="text">{{workTime}}</p>
            </div>
          </div>
        </div>
        <div class="sections">
          <div class="matter">
            <p class="title">服务地址:</p>
            <div class="edit">
              <p class="text">{{address}}</p>
            </div>
          </div>
        </div>
        <div class="sections">
          <div class="matter textarea">
            <p class="title">服务说明:</p>
            <div class="edit">
              <p class="text">{{description}}</p>
            </div>
          </div>
        </div>
      </div>

      <div class="tables" id="price-box">
        <div class="warp1">
          <ul style="background-color: #fef7ed;height: 100%;display: flex;border-radius: .05rem;">
            <li class="li"><span>服务单价</span><span class="monad">(元)</span></li>
            <li class="li"><span>总工时</span><span class="monad">(时)</span></li>
            <li class="li"><span>总价</span><span class="monad">(元)</span></li>
          </ul>
        </div>
        <div class="warp2">
          <ul style="height: 100%;display: flex;">
            <li class="li" style="color: #333333;"><span>{{price}}</span></li>
            <li class="li" style="color: #333333;"><span>{{hours}}</span></li>
            <li class="li" style="color: #333333;"><span>{{totalPrice}}</span></li>
          </ul>
        </div>
      </div>
    </div>
  </v-content>
  <v-user-info-pop :user_info="userInfo"></v-user-info-pop>
  <v-bookedpop :result_data="resultData" :job_id="jobId"></v-bookedpop>
  <v-footer-btn v-if="!(status==1 && orderType==0)">
    <input class="input ok" @click="optRecord(1)" value="发起" type="button" style="width: 100%;" v-if="status==0"/>
    <input class="input ok" @click="optRecord(1)" value="再次发起" type="button" style="width: 100%;" v-if="status==2"/>
    <input class="input no" @click="optRecord(2)" value="拒绝" type="button" v-if="status==1 && orderType==1"/>
    <input class="input ok" @click="optRecord(10)" value="同意" type="button" style="width: 68%;"
           v-if="status==1 && orderType==1"/>
    <input class="input max common" v-link="{name:'task-detail',params:{id:id}}" value="查看任务" type="button"
           v-if="status==10"/>
  </v-footer-btn>


</template>
<script>
  import $ from 'jquery'
  import VHeader from '../../components/Header'
  import VContent from '../../components/Content'
  import VFooterBtn from '../../components/FooterBtn'
  import VInnerBtn from '../../components/InnerBtn'
  import VBookedpop from '../../components/Bookedpop'
  import VUserInfoPop from '../../components/UserInfoPop'
  import {toast} from '../../assets/js/toast'
  import {url,lt} from '../../const'
  import {imgHost} from '../../filter'
  import {DJUtils} from '../../assets/js/common'

  export default{
    route: {
      data({from, to}){
        console.log('isReady:' + this.isReady);
        if (this.isReady) {
          return {
            state: !this.state
          }
        }
      }
    },
    ready(){

      this.$on('ready', this.init);
      this.$emit('ready');
      this.isReady = true;
    },
    data(){
      return {
        isReady: false,
        state: false,
        orderType: 0,
        optObj: {},

        id: 0,
        sn: '',
        jobId: '',
        buyName: '',
        buyIdentityNumber: '',
        sellName: '',
        sellIdentityNumber: '',
        buyer: '',
        seller: '',
        serviceName: '',
        categoryName: '',
        description: '',
        startTime: '',
        endTime: '',
        workSplit: '',
        price: '',
        address: '',
        workTime: '',
        status: '',
        rejectedReason: '',
        hours: 0,
        totalPrice: 0,
        remark:'',

        userInfo: {
          icon: '',
          nickName: '',
          sex: 0,
          age: 0,
          realName: '',
          phone: '',
          qq: '',
          wx: '',
          email: ''
        },
        resultData: {items: []}
      }
    },
    methods: {
      showRemark(){
         toast.message(this.remark, '拒绝原因');
      },
      setData(agreement){
        this.id = agreement.id;
        this.sn = agreement.sn;
        this.jobId = agreement.jobId;
        this.buyer = agreement.buyer;
        this.seller = agreement.seller;
        this.buyName = agreement.buyName;
        this.buyIdentityNumber = agreement.buyIdentityNumber;
        this.sellName = agreement.sellName;
        this.sellIdentityNumber = agreement.sellIdentityNumber;
        this.serviceName = agreement.serviceName;
        this.categoryName = agreement.categoryName;
        this.description = agreement.description;
        this.startTime = agreement.startTime;
        this.endTime = agreement.endTime;
        this.workSplit = agreement.workSplit;
        this.price = agreement.price;
        this.address = agreement.address;
        this.workTime = agreement.workTime;
        this.status = agreement.status;
        this.rejectedReason = agreement.rejectedReason;
        this.hours = agreement.hours;
        this.totalPrice = agreement.totalPrice;
        this.remark = agreement.rejectedReason;
        this.$nextTick(function () {
          let top = $('.feedback-box .feedback').height();
          let content = $('#section-box').height();
          let bottom = $('#price-box').height();
          $('.feedback-box').css('min-height', (top + content + bottom + 60) + 'px');
        });
      },
      init(){
        let vm = this;
        vm.$broadcast('login-check');
        let id = vm.$route.params.id;
        vm.orderType = vm.$route.query.orderType;
        console.log("orderType = " + vm.orderType);
        $('.bookedpop').css('display', 'none');
        vm.$http.get(url.agreementDetailShow, {'agId': id}).then(
                function ({data}) {
                  if (data.success) {
                    let agreement = data.data;
                    if (agreement) {
                      vm.setData(agreement);
                    }

                    let currUserId = lt.getJson(lt.keys.user).userId;
                    if (!vm.orderType || vm.orderType == undefined){
                      if(agreement.seller == currUserId){
                        vm.orderType = 1;
                      }else {
                        vm.orderType = 0;
                      }
                      console.log("orderType = " + vm.orderType);
                    }

                  } else {
                    toast.alert(data.resultMsg);
                  }
                }
        );
      },
      optRecord(status){
        let vm = this;
        let successMsg, conformTitle, needReason = false, optName;
        if (9 == status) {
          successMsg = '已删除';
          conformTitle = '确定删除？';
        } else if (1 == status) {
          successMsg = '已发起';
          conformTitle = '确定发起？';
        } else if (2 == status) {
          successMsg = '已拒绝';
          conformTitle = '确定拒绝？';
          needReason = true;
          optName = '拒绝';
        } else if (10 == status) {
          successMsg = '已同意';
          conformTitle = '确定同意？';
        } else {
          return;
        }

        if (!needReason) {
          toast.conform(conformTitle, function () {
            vm.$http.get(url.agreementStatus, {agId: vm.id, status: status}).then(
                    function ({data}) {
                      if (data.success) {
                        toast.alert(successMsg);
                        window.location.href = `#/order?status=${status}&&orderType=${this.orderType}`;
                      } else {
                        if (data.code == 201) {
                          vm.resultData.items = data.data;
                          console.log(vm.resultData);
                          setTimeout(function () {
                            $('.bookedpop').css('display', 'block');
                            $(".bookedpop").addClass('tran-scale1');
                            $(".bookedpop").fadeIn(100);
                            toast.mask(function () {
                              toast.hide_mask();
                              $('.bookedpop').css('display', 'none');
                            }, true);
                          $(".bookedpop").removeClass('tran-scale1');
                          }, 100);
                        } else {
                          toast.alert(data.resultMsg);
                        }
                      }
                    }
            )
          });
        } else {
          vm.optObj = {id: vm.id, status: status, successMsg: successMsg};
          toast.commit(vm.doCommit, `请说明${optName}原因`, `请填写${optName}原因`);
        }
      },

      doCommit(content){
        let vm = this;
        if (!content) {
          toast.alert('内容不能为空');
          return;
        }
        this.$http.get(url.agreementStatus, {
          agId: vm.optObj.id,
          status: vm.optObj.status,
          reason: content
        }).then(
                function ({data}) {
                  if (data.success) {
                    toast.alert(vm.optObj.successMsg);
                    toast.doCommit();
                    DJUtils.backHistory();
                  } else {
                    toast.alert(data.resultMsg);
                  }
                }
        );
      },

      showUserInfo (userId){
        let vm = this;
        console.log('userId=' + userId);
        vm.$http.get(url.agUserShow, {
          userId: userId
        }).then(
                function ({data}) {
                  if (data.success) {
                    vm.userInfo = data.data;
                    vm.userInfo.icon = imgHost(vm.userInfo.icon);
                    setTimeout(function () {
                      $('.userInfo').css('display', 'block');
                      $(".userInfo").addClass('tran-scale1');
                      $(".userInfo").fadeIn(100);
                      toast.mask(function () {
                        toast.hide_mask();
                        $('.userInfo').css('display', 'none');
                      }, true);
                      $(".userInfo").removeClass('tran-scale1');
                    }, 100);
                  } else {
                    toast.alert(data.resultMsg);
                  }
                }
        );
      },

      showService (serviceId){
        console.log('serviceId=' + serviceId);
        window.location.href = `#/service/${serviceId}`;
      }
    },

    watch: {
      'state': function () {
        this.$emit('ready');
      }
    },
    components: {VHeader, VContent, VFooterBtn, VInnerBtn, VBookedpop, VUserInfoPop}
  }
</script>
<style scoped>
  .feedback-box {
    border: .01rem solid #e4e4e4;
    box-shadow: 2px 2px 5px #d8d8d8;
    background-color: #fff;
    margin: .1rem;
    border-radius: .05rem;
    position: relative;
  }
  .feedback-box:before {
    box-shadow: -2px -2px 5px #D8D8D8;
    content: "";
    border-radius: .05rem;
    position: absolute;
    left: 0;
    right: 0;
    top: 0;
    bottom: 0;
    z-index: -1;
  }

  .feedback-box .feedback {
    margin: .25rem auto .15rem auto;
    width: 100%;
  }

  .titles {
    color: #333333;
    font-size: 25px;
    text-align: center;
  }

  .sn {
    color: #9a9a9a;
    font-size: 13px;
    text-align: right;
    margin: .1rem .1rem auto auto;
  }

  .line {
    padding: 0;
    margin-bottom: .25rem;
    height: 1px;
    overflow: hidden;
    font: 1px/1px simsun;
    border-bottom: .01rem dashed #c4c4c4;
  }

  .sections {
    height: auto;
    overflow: auto;
  }

  .sections .matter {
    height: auto;
    line-height: 1.8;
    font-size: .14rem;
  }

  .matter .textarea {
    height: auto;
    border-bottom: none;
  }

  .matter .title {
    margin-left: .1rem;
    float: left;
    height: 100%;
    width: .7rem;
  }

  .edit .text {
    color: #333;
  }

  .edit .text.none {
    color: #999;
  }

  .edit input[type='text'] {
    width: 100%;
    text-indent: 0;
    padding: 0;
  }

  .tables {
    height: 1rem;
    border: 1px solid #eba752;
    border-radius: .05rem;
    /* position: absolute; */
    /* width: 94%; */
    /* left: 3%; */
    margin: .2rem .1rem .2rem .1rem;
    /* bottom: .1rem; */
  }

  .warp1 {
    height: 45%;
  }

  .li {
    text-align: center;
    -webkit-box-flex: 1;
    flex: 1;
    margin-top: .15rem;
  }

  .li .monad {
    color: #bbbbbb;
  }

  .warp2 {
    height: 55%;
  }

  .inner-button .inner {
    padding: 0 .1rem;
  }

  .right-tag {
    background-image: url(../../assets/img/icon/tag.png);
    width: 75px;
    height: 65px;
    position: absolute;
    top: -.05rem;
    right: -.05rem;
    background-size: 375px 65px;
  }

  .status0 {
    background-position: -75px 0;
  }

  .status1 {
    background-position: 0 0;
  }

  .status2 {
    background-position: -150px 0;
  }

  .status3 {
    background-position: -225px 0;
  }

  .status4 {
    background-position: -300px 0;
  }

  .input.no {
    width: 30%;
    color: #00cda9;
    border: 1px solid;
  }

  /*.bookedpop {*/
    /*position: absolute;*/
    /*border-radius: .05rem;*/
    /*top: 25%;*/
    /*left: 12%;*/
    /*text-align: center;*/
    /*width: 76%;*/
    /*background-color: #fff;*/
    /*display: none;*/
  /*}*/
</style>


